.personal-form{
 display: flex;
 flex-direction: column;
 width: 100%;
 padding: 30px;
 background-color: aliceblue;
 padding-left: 20px;
 margin: 20px auto;
 border-radius: 20px;
}
.personal-form label{
 color: black;
 font-size: 20px;
 text-align: start;
}
.personal-form input{
 width: 100%;
 margin: 20px 0;
 padding: 5px;
}
.btn-add__user{
 background-color: blueviolet;
 color: #fff;
 font-size: 25px;
 padding: 15px 25px;
 border: none;
 border-radius: 8px;
}
.disabled{
 background-color: rgb(122, 116, 128);
 color: #fff;
 font-size: 25px;
 padding: 15px 25px;
 border: none;
 border-radius: 8px;
}